import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: 'login',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path:'/',
        component:Home,
        name:'main',
        hidden:true,
        children:[
            {path:'/main',name:'',component:Main}
        ]
    },
    // home 
    // UPS
    {
        path: '/',
        component: Home,
        name: 'UPS',
        // iconCls: 'el-icon-message',//class
        iconCls:['fa','fa-battery-full'],
        children: [
            {path:'/ups1',component:Table,name:'',iconCls:['fa','fa-battery-full']},
            {path:'/ups2',component:Form,name:'',iconCls:['fa','fa-battery-full']},
            {path:'/ups3',component:user,name:'',iconCls:['fa','fa-battery-full']},
            /* { path: '/main', component: Main, name: '' },
            { path: '/table', component: Table, name: 'Table' },
            { path: '/form', component: Form, name: 'Form' },
            { path: '/user', component: user, name: '' }, */
        ]
    },
    /* 
    AIR cool
    */
    {
        path:'/',
        component:Home,
        name:'Cool',
        iconCls:["fa"," fa-snowflake-o"],
        leaf:true,
        children:[
            {path:'/cool',component:Table,name:'',iconCls:["fa"," fa-snowflake-o"]}
        ]
    },

    /* 
    
    */
    {
        path:'/',
        name:'Power',
        component:Home,
        iconCls:["fa","fa-plug"],
        children:[
            {path:'/power1',name:'',component:echarts},
            {path:'/power3',name:'',component:echarts}
        ]
    },

    /* 
    IO
    */
    {
        path:'/',
        name:'IO',
        component:Home,
        leaf:true,
        iconCls:["fa"," fa-deaf"],
        children:[
            {path:'/io',name:'io',component:echarts,iconCls:["fa"," fa-deaf"]}
        ]
    },
    /* 
    
    */
    {
        path:'/',
        name:'Temperature humidity',
        component:Home,
        leaf:true,
        iconCls:["fa"," fa-thermometer-empty"],
        children:[
            {path:'/Temperature-humidity',name:'',component:echarts,iconCls:["fa"," fa-thermometer-empty"]}
        ]
    },

    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


